ng;\n    parameters: any;\n}\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst router = container.resolve<IRouter>(IRouter);\nconst zindex = container.resolve<IZindex>(IZindex);\nconst page = container.resolve<IPage>(IPage);\n\nconst props = defineProps<IPageProps>();\n\nconst containerDiv = ref<HTMLDivElement>();\nconst zindexValue = ref(zindex.getZIndex(IPage));\nconst contentWidth = computed(() => `calc(100% - 40px)`);\n\nfunction close() {\n    containerDiv.value?.classList.add(\"disapear\");\n    zindex.removeZIndex(IPage, zindexValue.value);\n    setTimeout(() => page.closePage(props.id), 500);\n}\n\nfunction logout() {\n    executor.execute(\n        async () => {\n            await executor.executeUseCase<ILogout>(ILogout);\n            router.push(\"/login\");\n            close();\n        },\n        { loading: true }\n    );\n}\n\n</script>\n<template>\n    <teleport to=\"body\">\n        <section class=\"rootSection\" :style=\"{ zIndex: zindexValue }\">\n            <section class=\"columnsSection\">\n                <section class=\"contentSection\" :style=\"{ width: contentWidth }\">\n                    <div ref=\"containerDiv\" :style=\"{ zIndex: zindexValue }\">\n                        <div class=\"header\">\n                            <span class=\"prev-button\" @click=\"close\"><i class=\"mdi mdi-arrow-left-box mdi-30px\"\n                                    style=\"font-size: 30px;\"></i></span>\n                            <div class=\"icon-container small\">\n                                <img src='../../../../images/is-bankasi-logo.jpg' alt=\"is-bankasi-logo\" class=\"header-logo\" width=\"16\"\n                                    height=\"16\" />\n                            </div>\n                            <label class=\"app-name\">{{ viewModel.studio.appName }}</label>\n                            <label class=\"app-name\" style=\"margin-left: 12px;\">/</label>\n                            <label class=\"page-name-text\">{{ props.options.name }}</label>\n                            <div style=\"display: flex; margin-left: 1450px;\">\n                                <i class=\"mdi mdi-account-circle profile-icon\"></i>\n                                <i class=\"mdi mdi-chevron-down chevronIcon\" data-bs-toggle=\"dropdown\"\n                                    aria-expanded=\"false\" data-bs-target=\"#dropdown-menu\" id=\"dropdownMenuButton1\"\n                                    style=\"padding-top: 16px; margin-right: -11px\"></i>\n                                <ul style=\"width: 246px\" class=\"dropdown-menu\" aria-labelledby=\"dropdownMenuButton1\">\n                                    <li>\n                                        <div class=\"menuÄ±tem\" style=\"display: flex; margin: 9px\">\n                                            <i class=\"mdi mdi-account-circle\" style=\"font-size: 25px\"></i>\n                                            <a style=\"margin: 7px\">{{ viewModel.user?.email }}</a>\n                                        </div>\n                                    </li>\n                                    <li>\n                                        <div class=\"menuÄ±tem\" style=\"display: flex; margin: 9px\" @click=\"logout()\">\n                                            <i class=\"mdi mdi-logout\" style=\"font-size: 25px\"></i>\n                                            <a style=\"margin: 7px\">Logout</a>\n                                        </div>\n                                    </li>\n                                </ul>\n                            </div>\n                        </div>\n                        <component :is=\"props.component\" v-bind=\"props.parameters\" />\n                    </div>\n                </section>\n            </section>\n        </section>\n    </teleport>\n</template>\n<style scoped>\n.rootSection {\n    top: 0%;\n   